.card {
  margin: 20px;
  border-radius: 10px;
  transition: transform 0.1s ease-in;
  cursor: pointer;
  overflow: hidden;
}

.card:hover {
  transform: scale(1.05);
}

.card > img {
  object-fit: contain;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  height: auto;
}

.card__info {
  margin-top: -9px;
  border-radius: 10px;
  padding: 0px;
  padding-top: 20px;
  border: 1;
}

.card__info > h2 {
  font-size: 18px;
  font-weight: 600;
}

.card__info > h4 {
  font-size: 14px;
  font-weight: 300;
  margin-top: 8px;
  margin-bottom: 8px;
}
